@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #1E90FF;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  text-align: center;
}

h1 {
  color: #fff;
  margin-bottom: 0.5rem;
  font-size: 1.75rem;
}

.Cover {
  background: #0047AB;
  margin-top: 5rem;
  padding: 2rem;
  border-radius: 5px;
}

.AppForm {
  width: 100%;
  margin-bottom: 1rem;
}

.TodoInput {
  outline: none;
  background: none;
  border: 1px solid #1E90FF;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  width: 300px;
  color: #fff;
}

.TodoInput::placeholder {
  color: #C0C0C0;
}

.TodoBtn {
  background: #1E90FF;
  color: #fff;
  border: none;
  padding: 0.55rem;
  cursor: pointer;
}

.Tasks {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #1E90FF;
  color: #fff;
  padding: 0.75rem 1rem;
  border-radius: 5px;
  margin-bottom: 1rem;
}

.delete-icon {
  margin-left: 0.75rem;
}

.incompleted {
  color: #fff;
  cursor: pointer;
}

.completed {
  color: hsla(0, 0%, 100%, 0.801);
  text-decoration: line-through;
  cursor: pointer;
}

.edit-icon,
.delete-icon {
  cursor: pointer;
}
 